@import "main";
@import "spritePNG";
@import "mixins/calc";
@import "_page";
@import "blocks/article";
@import "blocks/about";

//  
.main-banner {
  margin-bottom: 70px;
  &__first-row {
    // width: 100%
    margin: 0;
    padding-top: 41px;
    padding-bottom: 41px;
    background: linear-gradient(60deg, #1422aa, #a3d3e5);
    background-size: 400% 400%;
    animation: AnimationName 25s ease infinite;
    @keyframes AnimationName {
      0% {
        background-position: 0% 82%; }
      50% {
        background-position: 100% 19%; }
      100% {
        background-position: 0% 82%; } } }
  &__title {
    @include ff-roboto-bold;
    font-size: 3.75em;
    color: $orange;
    text-transform: none;
    text-decoration: none;
    @media(max-width: 400px) {
      font-size: 3em; } }
  &__text, p {
    margin-top: 15px;
    @include ff-roboto;
    font-size: 3em;
    font-weight: 400;
    line-height: 1.12em;
    color: #fff;
    @media(max-width: 400px) {
      font-size: 2em; } }

  &__statistic {
    margin-top: 97px; }
  &__stat-item {
    $indent: 44px;
    $hb: 48px;
    padding-left: $indent;
    position: relative;
    color: #fff;
    line-height: $hb;
    span {
      font-size: 1.63em;
      line-height: $hb; }
    &::before {
      @include psblock(block, absolute); }
    &--people {
      &::before {
        @include sprite($people);
        @include leftcalc($people-width, $hb);
        @include topcalc($people-height, $hb); } }
    &--coin {
      &::before {
        @include sprite($coin);
        @include leftcalc(30px, $hb);
        @include topcalc(28px, $hb); } }
    &--building {
      &::before {
        @include sprite($building);
        @include leftcalc(18px, $hb);
        @include topcalc(28px, $hb); } }
    &--percent {
      &::before {
        @include sprite($percent);
        @include leftcalc(29px, $hb);
        @include topcalc(29px, $hb); } } }
  &__info {
    width: 100%;
    padding-top: 33px;
    padding-bottom: 34px;
    box-shadow: 0 0 7px rgba(102, 102, 102, 0.21);
    &-item {
      display: block;
      margin-bottom: 33px;
      $info-pd-left: 66px;
      padding-left: $info-pd-left;
      h4 {
        margin: 0;
        font-size: 1.125em;
        line-height: 1.125em;
        color: $accent; }
      p {
        margin: 0;
        font-size: 0.875em;
        line-height: 1.285em;
        color: $headgray; }
      &::before {
        @include psblock(block, absolute); }
      &--licence {
        &::before {
          @include sprite($licence);
          @include leftcalc($licence-width, $info-pd-left);
          top: 0; } }
      &--official {
        &::before {
          @include sprite($display);
          @include leftcalc($display-width, $info-pd-left);
          top: 0; } }
      &--free {
        &::before {
          @include sprite($medal);
          @include leftcalc($medal-width, $info-pd-left);
          top: 0; } } } } }

//  
.subscribe {
  width: 100%;
  margin-bottom: 30px;
  padding: 22px 26px;
  border: 2px solid $accent;
  &__legend {
    margin: 0;
    padding: 0 0 24px 0;
    border: none;
    font-size: 1em;
    color: #000;
    span {
      @include ff-arial-bold;
      color: $accent;
      font-style: italic; } }
  &__input {
    margin-bottom: 11px;
    padding: 6px 18px;
    font-size: 1em;
    line-height: 1em;
    height: 2.5em;
    border: 1px solid #bcbcbc;
    color: $textgray;
    background-color: #fff; }
  &__check {
    margin: 22px 0 17px; }
  &__checkbox {
      &--fa {
        @include chbx-fa($accent, 10px);
        font-size: 2.5em; } }
  &__check-label {
    margin: 0 0 0 10px;
    padding: 0;
    @include ff-arial; }
  &__btn {
    margin: 0;
    padding: 9px 32px;
    font-size: 1em;
    line-height: 1em; } }

