.article-prevs-block {
  &__1st-line {
    display: flex;
    flex-direction: row;
    align-content: stretch;
    flex-wrap: wrap;
    justify-content: stretch; } }

// 1- - 
.article-1st {
  margin-bottom: 30px;
  overflow: hidden;
  background-image: url("../img/article_photo.png");
  background-repeat: no-repeat;
  background-clip: content-box;
  background-size: cover;
  &__wrap {
    display: flex;
    flex-direction: column;
    padding: 40px 100px 14px 46px;
    width: 100%;
    height: 100%;
    background: linear-gradient(to top, rgba(255, 255, 255, 0), $accent);
    @media(max-width: 400px) {
      padding: 20px 20px 14px 20px; } }
  &__title {
    flex-grow: 0;
    margin: 0 0 30px 0;
    color: #fff;
    font-size: 2.25em;
    @media(max-width: 400px) {
      font-size: 1.75em; } }
  &__link {
    color: #fff;
    &:hover {
      color: #fff; } }
  &__text {
    flex-grow: 3;
    color: #fff; }
  &__statistic {
    flex-grow: 0;
    .text-muted {
      span {
        color: #000;
        font-size: 1em; } } } }

// - 
.article-prev {
  margin-bottom: 30px;
  position: relative;
  align-self: stretch;
  &__img-wrap {
    display: block;
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 30px;
    height: 220px;
    overflow: hidden;
    img {
      width: 100%;
      height: auto;
      // object-fit: cover
 }      // object-position: left top
    &:hover {
      transform: scale(1.05, 1.05); } }
  &__tag {
    display: block;
    text-decoration: none;
    @include ff-arial-bold;
    color: $lightgray;
    font-size: 0.875em;
    line-height: 1.285em;
    &:hover {
      color: $lightgray; }
    &:visited {
      color: $lightgray; } }
  &__link {
    text-decoration: none; }
  &__title {
    margin-top: 6px;
    margin-bottom: 21px;
    @include ff-arial-bold;
    color: $headgray;
    font-size: 1.125em;
    line-height: 1.125em; }
  &__text {
    @include ff-arial;
    color: $textgray;
    font-size: 0.875em;
    line-height: 1.714em; }
  &--margin-bottom {
    margin-bottom: 60px; } }


//  
.section-head {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
  margin-top: 70px;
  margin-bottom: 27px;
  width: 100%;
  &__line {
    position: absolute;
    top: 50%;
    left: $pad;
    margin: 0;
    padding: 0;
    z-index: -1;
    width: calc(100% - (2 * #{$pad}));
    height: 1px;
    background-color: #545454; }
  &__title {
    margin: 0;
    padding: 0 15px 0 0;
    display: inline-block;
    z-index: 10;
    background-color: #fff;
    @include ff-arial-bold;
    font-size: 1.125em;
    line-height: 1.33em;
    color: $accent;
    &:hover {
      text-decoration: underline; } }
  &__all-articles-link {
    margin: 0;
    padding: 0 0 0 15px;
    display: inline-block;
    background-color: #fff;
    @include ff-arial;
    font-size: 1em;
    line-height: 1.5em;
    color: $accent;
    &--title {
      padding: 0; }
    &:hover {
      color: $accent; } } }


