.announcement {
  width: 100%;
  padding-top: 30px;
  padding-bottom: 46px;
  background-color: #f3efe3;
  $day-width: 115px;
  &__title {
    margin-bottom: 46px;
    @include ff-arial-bold;
    color: $accent;
    font-size: 1.125em;
    line-height: 1.33em; }
  &__item {
    display: flex;
    flex-direction: row;
    margin-bottom: 30px; }
  &__date {
    display: flex;
    flex-direction: column;
    align-content: center;
    position: relative;
    text-align: center;
    @include ff-arial-bold;
    color: $accent;
    font-size: 1.125em;
    &:before {
      @include psblock(block, absolute);
      @include sprite($calendar);
      @include leftcalc($calendar-width, $day-width);
      z-index: 1;
      top: 0; } }
  &__day {
    padding-top: 9px;
    width: $day-width;
    z-index: 12;
    text-align: center;
    font-size: 1.33em; }
  &__month {
    @include ff-arial;
    text-align: center;
    width: $day-width; }
  &__text {
    display: block;
    @include ff-arial-bold;
    color: $headgray;
    font-size: 1em;
    line-height: 1.5em;
    &:hover {
      color: $headgray; } } }

// FAQ
.faq {
  margin-top: 44px;
  &__section {
    margin-bottom: 21px; }
  &__item {
    margin-bottom: 32px; }
  &__tag {
    display: block;
    position: relative;
    @include ff-arial-bold;
    font-size: 0.875em;
    color: $active-color;
    span {
      padding-left: 12px; }
    &:hover {
      color: $active-color;
      cursor: pointer; } }

  &__quest {
    display: block;
    margin-top: 15px;
    margin-bottom: 4px;
    @include ff-arial-bold;
    color: #000;
    font-size: 1.125em;
    line-height: 1.2em;
    &:hover {
      color: #000;
      cursor: pointer; } } }

