.box-programm {
	box-shadow: 0 3px 5px rgba(#000000, 0.1);
	border: 1px solid transparent;
	margin-bottom: 5em;
	position: relative;
	&_header {
		position: relative;
		padding: 1.5em 2em 1em 2em;
		overflow: hidden;
		z-index: 2;
		p {
			margin: 0;
			padding: 0; }
		.blur {
			background-repeat: no-repeat;
			background-size: cover;
			background-position: center center;
			background-blend-mode: overlay;
			position: absolute;
			top: 0;
			right: 0;
			left: 0;
			bottom: 0;
			filter: blur(3px);
			z-index: 9;
			width: 100%;
			height: 100%;
			transform: scale(1.05); }
		.blur-blue {
			background-color: $accent; }
		.blur-black {
			background-color: rgba(#000000, 0.6); }
		.content {
			z-index: 100;
			color: #ffffff;
			position: relative;
			h4 {
				margin-top: 2em; } }
		.star {
			position: absolute;
			top: 15px;
			right: 15px;
			z-index: 10;
			i {
				color: #FED073; }
			span {
				position: absolute;
				top: 55%;
				left: 50%;
				transform: translate(-50%, -50%);
				color: #ffffff; } } }
	.mark {
		position: absolute;
		top: -15px;
		left: 30px;
		z-index: 10;
		color: #ffffff;
		padding: 0 1.5em;
		height: 30px;
		text-align: center;
		font-weight: 700;
		line-height: 30px; }
	.color-mark {
		&_blue {
			background-color: $accent; }
		&_red {
			background-color: red; }
		&_orange {
			background-color: #FED073; }
		&_lightblue {
			background-color: #AED3E5; } }
	&_main {
		padding: 0.5em 1em 0.5em 2em;
		color: $accent;
		background-color: #ffffff;
		z-index: 2; }
	&_footer {
		display: flex;
		justify-content: space-between;
		padding: 1.8em 1em 1em 1em;
		z-index: 2;
		.left-content {
			.fa-ul {
				color: #5A5A5A;
				li {
					margin-bottom: 5px; }
				.fa-li {
					i {
						color: #fed07f; } } } }
		.right-content {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			.img-wrap {
				display: flex;
				justify-content: center;
				align-items: flex-start;
				flex-wrap: wrap;
				overflow: hidden;
				width: 100px;
				.img-circle {
					flex: 1 40px !important;
					overflow: hidden;
					margin: 4px;
					border-radius: 0;
					img {
						border-radius: 50%;
						width: 100%;
						height: 100%;
						object-fit: cover; }
					span {
						overflow: hidden;
						display: block;
						width: 40px;
						height: 40px;
						text-align: center;
						line-height: 38px;
						border: 2px solid #999999;
						border-radius: 50%; } }
				.img-circle:nth-child(odd) {
					flex: 0 40px; }
				.img-circle:nth-child(even) {
					flex: 0 40px; } }
			.rating {
				margin-top: 5px;
				color: #fed07f;
				font-size: 12px; } } }
	&_button {
		position: absolute;
		bottom: -60px;
		left: -1px;
		right: 0;
		width: calc(100% + 2px);
		opacity: 0;
		background-color: #ffffff;
		.wrap-button {
			padding: 0.8em 1.5em;
			display: flex;
			justify-content: space-between;
			align-items: center;
			.button-primary {
				display: block;
				border-radius: 100px;
				padding: 0.8em 1.5em;
				font-size: 14px;
				color: #fff;
				padding: 1em 2em;
				background-color: $accent;
				@include mt(0.25s);
				&:hover {
					background: $light-blue; } }
			.button-default {
				display: block;
				padding: 0.8em 1.5em;
				font-size: 14px;
				background-color: #F6F6F6;
				border: 1px solid #BCBCBC;
				border-radius: 100px;
				color: #000000;
				&:hover {
					text-decoration: none; } } } }
	&:hover {
		border: 1px solid $accent;
		.box-programm_button {
      z-index: 11;
      opacity: 1;
      box-shadow: 0 3px 5px rgba(#000000, 0.1);
      border-left: 1px solid $accent;
      border-right: 1px solid $accent;
      border-bottom: 1px solid $accent; } } }

