      .logo {
        img {
          width: 60px;
          height: auto;
          float: left;
          margin-right: 10px; } }

      .logo-title {
        font-size: 15px;
        font-family: $headline-font;
        display: inline-block;

        span {
          font-weight: 400;
          font-size: 10px;
          font-family: $default-font;
          text-transform: uppercase;
          letter-spacing: 0.60em; } } } }
  .text-columns-5 {
    column-count: 5; }
  .text-columns-2 {
    column-count: 2; }
  .s-header {
    .logo {
      img {
        width: 70px;
        height: auto; } } }
  .s-facts {
