          color: #ffffff;

          &:hover {
            text-decoration: none;
            color: $orange; } } } }

    i {
      font-size: 20px; }

    span {
      font-size: 16px;

      small {
        font-size: 13px; } } }

  .s-loyalty {
    .item-loyalty {
      span {
        font-size: 1.5em; } } }

  .minors-wrap {
    .item-minor {
      &-bottom {
        .btn-wrap {}
        font-size: 1em; } } }

  //    
  .study-wrap {
    .panel-body {
      a.btn-block {
        font-size: 0.8em; } } }

  //      
