//  

//       
// $sh -  
// $fh -  
@mixin topcalc($sh, $fh) {
  $top-point: calc((#{$fh} / 2) - (#{$sh} / 2));
  @if #{$top-point} > 0 {
    top: $top-point;
  }
  @else {
    top: 0;
  }
}

//    
// $sw -  
// $fw -  
@mixin leftcalc($sw, $fw) {
  $left-point: calc((#{$fw} / 2) - (#{$sw} / 2));
  @if #{$left-point} > 0 {
    left: $left-point;
  }
  @else {
    left: 0;
  }
}

//  -
// $display - - display
// $pos - c- position
@mixin psblock($display, $pos) {
  content: '';
  display: $display;
  position: $pos;
}

//   checkbox'      @fontawesome
@mixin chbx-fa($col, $indent, $font: 'Font Awesome 5 Free') {
  display: none;

  & + label:before {
    font-family: $font;
    display: inline-block;
    content: '\f111';
    letter-spacing: $indent;
    color: $col;
  }

  &:checked + label:before {
    content: '\f058';
  }
}
