header {
  .s-header {
    padding: 40px 0;
    background-color: #fff;

    .logo {
      img {
        width: 70px;
        height: auto;
        float: left;
        margin-right: 10px; } }

    .logo-title {
      font-size: 16px;
      font-family: $headline-font;
      display: inline-block;

      span {
        font-weight: 400;
        font-size: 10px;
        font-family: $default-font;
        text-transform: uppercase;
        letter-spacing: 0.60em; } }

    .header-date, .header-phone {
      font-size: 0.85em;

      &-label {
        color: #999999;
        font-size: 0.85em; } }

    .header-phone {
      &-number {
        margin-top: 0.3em;

        a {
          font-size: 1.5em;
          color: #5A5A5A; } } }

    .work-hours {
      font-size: 1.5em;
      margin-top: 0.3em;
      color: #5A5A5A; }

    .btn-wrap {
      .header-cart {
        position: absolute;
        bottom: -65%;
        left: 0;
        right: 0;

        a {
          color: $accent; } }

      .header-btn {
        position: relative;

        .btn-group.open button {
          z-index: 1001; }

        .btn-login {
          i {
            color: $accent;
            margin-right: 10px;
            transition: all 0.3s; }

          &:hover {
            i {
              color: #fff; } } }

        .btn-user {
          position: relative;
          text-overflow: ellipsis;
          overflow: hidden;
          z-index: 1001;
          padding: 1em 1.7em 1em 0.8em;
          font-size: 0.9em;

          span.caret {
            position: absolute;
            right: 18px;
            top: 50%;
            transform: translateY(-50%); } }
        //    
        .dropdown-menu {
          position: absolute;
          top: 50%;
          width: 100%;

          li:first-child {
            padding-top: 25px; }

          li {
            padding: 5px 0;

            i {
              margin-left: 7px;
              color: $accent; } }

          li.divider {
            padding: 0;
            margin: 5px 0; } } } } } }

