.s-carousel {
  padding: 0;
  margin: 0;

  .col-lg-12 {
    padding: 0; }

  .carousel {
    width: 100%;

    .carousel-control {
      background-image: none;
      width: 10%;
      text-shadow: none; }

    .carousel-inner {
      .item {
        img {
          width: 100%;
          background-size: cover;
          display: block;
          background-position: center center; } } }

    .glyphicon-menu-left {
      position: absolute !important;
      top: 50%;
      right: 20%;
      transform: translateY(-50%);
      font-size: 40px;
      color: $accent;

      &::after {
        position: absolute;
        content: '';
        width: 25px;
        height: 5px;
        background-color: $accent;
        top: 50%;
        left: 10px;
        transform: translateY(-50%); } }

    .glyphicon-menu-right {
      position: absolute !important;
      top: 50%;
      left: 20%;
      transform: translateY(-50%);
      font-size: 40px;
      color: $accent;

      &::after {
        position: absolute;
        content: '';
        width: 25px;
        height: 5px;
        background-color: $accent;
        top: 50%;
        right: 10px;
        transform: translateY(-50%); } } } }

