.s-story {
  padding-bottom: 50px;

  .text {
    padding-bottom: 30px;
    font-size: 18px; }

  .item-story {
    padding: 25px;
    margin: 0;
    min-height: 400px;
    height: 400px;
    border: 12px solid #fff;
    box-sizing: border-box;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      background-color: rgba(255, 255, 255, 0.1);
      box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
      width: 100%;
      height: 100%;
      top: 0;
      left: 0; }

    .year {
      color: $accent;
