      font-size: 14px;
      color: #333333;
      line-height: 1.65em; } }

  .bg-wrap {
    background-image: url('/img/logo.svg');
    background-size: cover;
    background-position: center left;
    width: 100%;
    height: auto;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      display: block;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(255, 255, 255, 0.97); }

    .col-lg-3 {
      padding: 0; } } }

