.s-facts {
  .container-fluid {
    padding: 0;
    margin: 0; }

  .facts-wrap {
    background-image: url('/img/bg-foto.jpg');
    background-size: cover;
    background-position: center center; }

  .facts-grid {
    display: grid;
    width: 100%;
    grid-gap: 1px;
    height: auto;
    grid-template-areas: "item-1 item-2 item-3 item-4" "item-5 item-2 item-6 item-7" "item-8 item-9 item-11 item-7" "item-8 item-10 item-11 item-12";

    .item-1 {
      grid-area: item-1;

      .flipper-back {
        background-color: $highlight; } }

    .item-2 {
      grid-area: item-2;

      .flipper-back {
        background-color: $accent;
        color: #fff; } }

    .item-3 {
      grid-area: item-3;

      .flipper-back {
        background-color: $active-color; } }

    .item-4 {
      grid-area: item-4;

      .flipper-back {
        background-color: $highlight; } }

    .item-5 {
      grid-area: item-5;

      .flipper-back {
        background-color: $active-color; } }

    .item-6 {
      grid-area: item-6;

      .flipper-back {
        background-color: $highlight; } }

    .item-7 {
      grid-area: item-7;

      .flipper-back {
        background-color: $accent;
        color: #fff; } }

    .item-8 {
      grid-area: item-8;

      .flipper-back {
        background-color: $accent;
        color: #fff; } }

    .item-9 {
      grid-area: item-9;

      .flipper-back {
        background-color: $active-color; } }

    .item-10 {
      grid-area: item-10;

      .flipper-back {
        background-color: $highlight; } }

    .item-11 {
      grid-area: item-11;

      .flipper-back {
        background-color: $accent;
        color: #fff; } }

    .item-12 {
      grid-area: item-12;

      .flipper-back {
        background-color: $active-color; } }

    span {
      font-size: 40px;
      display: block;
      color: #fff;
      font-weight: 500; }

    .item {
      position: relative;
      display: block;
      width: 100%;
      height: 100%;
      perspective: 800px;
      cursor: pointer;

      &::after {
        position: absolute;
        top: 0;
        left: 0;
        content: '';
        display: block;
        width: 100%;
        height: 100%; }

      .flipper-card {
        width: 100%;
        height: 100%;
        transform-style: preserve-3d;
        transition: all 1.1s cubic-bezier(0.175, 0.885, 0.32, 1.275);
        border: 2px solid #fff;

        .flipper-back {
          position: relative;
          padding: 30px 50px;
          width: 100%;
          height: 100%;
          display: block;
          transform: rotateY(180deg); }

        .flipper-front,
        .flipper-back {
          backface-visibility: hidden; } } } } }

.flipper-card.flipper-is-flipped {
  transform: rotateY(180deg); }

