.programs__card {
  margin-bottom: 30px;

  .programs__card-block {
    position: relative;

    &:hover .programs__card-main {
      background-size: 105%;

      &::before {
        background: rgba(0, 0, 0, 0.6) !important;
        z-index: 0; } }

    .programs__card-main {
      position: relative;
      background-repeat: no-repeat;
      background-position: center center;
      background-size: 100%;
      color: #fff;
      height: auto;
      padding: 25px 65px 65px 30px;
      position: relative;
      transition: background-size 0.3s linear;

      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: block;
        background: rgba(0, 0, 0, 0.1);
        z-index: 0;
        transition: all 0.3s ease; }

      .programs__item-type {
        position: absolute;
        left: 30px;
        margin: 0;
        bottom: 24px;
        z-index: 5;
        color: #fff;

        a {
          color: #fff !important;

          &:hover {
            color: #a3d3e5 !important; } } }

      &.v1::before {
        background: rgba(0, 0, 0, 0.5); }

      &.v2::before {
        background: rgba(20, 0, 0, 0.5); }

      &.v3::before {
        background: rgba(0, 20, 0, 0.5); }

      &.v4::before {
        background: rgba(0, 0, 20, 0.5); }

      &.v5::before {
        background: rgba(0, 20, 20, 0.5); }

      &.v6::before {
        background: rgba(20, 0, 20, 0.5); }

      &.v7::before {
        background: rgba(20, 20, 0, 0.5); }

      &.v8::before {
        background: rgba(20, 20, 20, 0.5); }

      .programs__card-content {
        position: relative;

        .programs__item-topic {
          font-size: 14px;
          margin-bottom: 25px;

          a {
            color: #fff;
            transition: all 0.25s;

            &:hover {
              color: $active-color; } } }

        .programs__item-title {
          font-size: 18px;
          text-transform: uppercase;

          a {
            color: #fff;
            transition: all 0.25s;

            &:hover {
              color: $active-color; } } } }

      .programs__card-star {
        width: 40px;
        height: 40px;
        position: absolute;
        display: block;
        color: #fff;
        top: 15px;
        right: 15px;
        font-size: 12px;
        text-align: center;
        line-height: 40px;
        z-index: 5;
        font-weight: 700;

        &::before {
          content: '\f005';
          font-family: $font-awesome;
          font-weight: 900;
          position: absolute;
          top: -3px;
          left: -2px;
          width: 100%;
          height: 100%;
          font-size: 40px;
          color: $highlight;
          z-index: -1; } } }
    //       
    .programs__card-bottom {
      padding: 30px;
      background: #fff;

      .programs__card-info {
        margin-bottom: 18px;
        font-size: 0;

        &-item {
          display: inline-block;
          vertical-align: top;
          margin-right: 16px;
          margin-bottom: 12px;
          white-space: nowrap;
          color: #5a5a5a;
          font-size: 14px;

          &:last-child {
            margin-right: 0 !important; }

          i {
            color: $highlight;
            font-size: 12px; } } } } } }

