.razvitum-ordered-list {
  ol, ul {
    list-style-type: none;
    margin-left: 2em;
    padding-left: 0; }

  ol > li, ul > li {
    counter-increment: customlistcounter;
    margin-bottom: 1em; }

  ol > li:before, ul > li:before {
    content: counter(customlistcounter) " ";
    font-weight: bold;
    font-size: 150%;
    color: $highlight;
    float: left;
    width: 2em; }

  ol:first-child, ul:first-child {
    counter-reset: customlistcounter; } }

