.tabletree {
  margin: 0 !important;
  padding: 0 !important;
  line-height: normal;
  /*word-wrap: break-word*/
  /*word-break: break-all*/



  ul {
    position: relative;
    display: table-cell;
    margin: 0 !important;
    padding: 0 0 0 60px !important;
    vertical-align: middle; }

  li {
    display: table;
    position: relative;
    margin: 0 !important;
    padding: 0 0 0 58px !important; }

  div {
    display: table-cell;
    vertical-align: middle;
    position: relative;
    width: auto;
    height: 180px; }

  ul {
    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      width: 60px;
      height: 50%;
      border-bottom: 2px solid #999; }

    li {
      &:after, &:not(:last-child):not(:first-child):before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 60px;
        height: 50%;
        border-bottom: 2px solid #999; } }

    &:before {
      left: 0; }

    li {
      &:not(:only-child) {
        &:after {
          border-left: 2px solid #999; }

        &:first-child:after {
          top: 54%;
          border-top: 2px solid #999;
          border-bottom-color: transparent; } }

      &:not(:last-child):not(:first-child):before {
        height: 101%;
        border-left: 2px solid #999;
        border-bottom-color: transparent; } } }

  span {
    display: inline-block;
    width: 100px;
    height: 100px;
    background-color: #999999;
    border-radius: 50%;
    position: relative;
    margin: 0;
    overflow: hidden; }

  p {
    text-align: center;
    display: block;
    position: absolute;
    top: 50%;
    padding-top: 55px;
    font-size: 12px;
    width: 200px;
    left: -50%;

    strong {
      color: $accent; } } }

