.bg1-seminar-about {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-blend-mode: multiply;
  background-color: $accent;
  position: relative;

  .video-bg-wrap {
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: linear-gradient(90deg, $accent 50%, transparent 50.1%);

    video {
      min-width: 100%;
      min-height: 100%;
      width: auto;
      height: auto;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      mix-blend-mode: multiply;
      z-index: 1; } } }

@supports not (mix-blend-mode: multiply) {
  .bg1-seminar-about {
    .video-bg-wrap {
      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $accent;
        opacity: 0.7;
        z-index: 2; } } } }

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .bg1-seminar-about {
    .video-bg-wrap {
      &::after {
        content: "";
        position: absolute;
        top: 0;
