.tree {
  min-height: 20px;
  padding-top: 3px;
  padding-right: 8px;
  padding-left: 0;
  padding-bottom: 3px;
  margin-bottom: 20px;

  li {
    list-style-type: none;
    margin: 0;
    padding: 10px 5px 0 5px;
    position: relative;

    &::before, &::after {
      content: '';
      left: -20px;
      position: absolute;
      right: auto; }

    &::before {
      border-left: 1px solid #999;
      bottom: 50px;
      height: 100%;
      top: 0;
      width: 1px; }

    &::after {
      border-top: 1px solid #999;
      height: 20px;
      top: 25px;
      width: 25px; }

    div.span {
      -moz-border-radius: 5px;
      -webkit-border-radius: 5px;
      border: 1px solid #999;
      border-radius: 5px;
      display: inline-block;
      padding: 5px 10px;
      text-decoration: none; }

    &.parent_li > div.span {
      cursor: pointer; }

    &:last-child::before {
      height: 26px; }

    &.parent_li > div.span:hover {
      background: #eee;
      border: 1px solid #94a0b4;
      color: #000;

      + ul li div.span {
        background: #eee;
        border: 1px solid #94a0b4;
        color: #000; } }

    h3 {
      display: inline-block;
      vertical-align: middle; }

    a {
      h3 {
        margin: 0; } }

    a:hover {
      h3 {
        text-decoration: none; } } }

  & > ul {
    padding-left: 0;

    & > li {
      padding-left: 0; } }

  > ul {
    padding-left: -40px;

    > li {
      &::before, &::after {
        border: 0; } } } }


